.banner{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;    
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("/assets/backgrounds/img5.jpg");
    background-attachment:fixed ;
    text-align: center;
    margin: 0 auto
}

.img-baner {
    width: 7.5vw;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.img-baner img{
    margin: 40px;
    height: 80px;
    transition:  0.2s ease;}

.img-baner img:hover{
    margin: 30px;
    height: 100px;
    transition:  0.2s ease;
}

.banner-text{
    color: white;
}

.arp-title{
    font-weight: 700!important;
}
